@use "global" as *;

.pagenavi {

}

.pagenavi__inner {
    // margin: auto;
    // // max-width: rem(600);
    // width: 100%;
    // @include mq(md){
    //     // max-width: rem(1200);
    // }
}

.pagenavi .wp-pagenavi {
	clear: both;
	text-align: center;

    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin-left: -1px;
    // justify-content: space-between;
    // display: flex;
    // @include mq("md") {
    //     display: block;
    // }
}

.pagenavi.page-blogDetail__pagenavi .wp-pagenavi {

    @include mq("md") {
        margin-left: rem(-34);
    }
}

.pagenavi .wp-pagenavi a,
.pagenavi .wp-pagenavi span {
    color: $green1;
    margin: rem(0) rem(6);
	// padding: rem(11) rem(14) rem(11) rem(14);
    font-size: rem(16);
    line-height: 1;
	white-space: nowrap;
    border: 1px solid $green1;
	background-color: $white;
    transition: .3s;
    // min-width: rem(40); /*  */
    width: 38px;
    height: 38px;
    display:flex;
    align-items:center;
    justify-content:center;

    @include mq("md") {
        // padding: 11px 14px;
    }
}

.pagenavi .wp-pagenavi a:hover {
	color: #fff;
	background-color: $green1;
	border: 1px solid $green1;
    opacity: 1;
}

.pagenavi .wp-pagenavi .current {
	color: #fff;
	background-color: $green1;
	border: 1px solid $green1;
}

.pagenavi .wp-pagenavi .previouspostslink {
    position: relative;
    margin-right: rem(33);
    margin-left: rem(0);
    width: 38px;
    height: 38px;
    // padding: rem(18) rem(18);

    // @include mq("md") {
    //     margin-right: rem(36);

    // }
}

.pagenavi.page-blogDetail__pagenavi .wp-pagenavi .previouspostslink {
    margin-right: rem(41);
}

.pagenavi .wp-pagenavi .previouspostslink::after {
    content: "";
    position: absolute;
    top: 36%;
    left: 42%;
    transform: translate(-50%, -50%);
    width: rem(10); /*  */
    height: rem(10); /*  */
    border-top: 1px solid $green1; /* */
    border-right: 1px solid $green1;/* */
    display: inline-block; /*  */
    transform: rotate(-135deg); /*  */

}


.pagenavi .wp-pagenavi .nextpostslink {
    position: relative;
    margin-left: rem(33);
    margin-right: rem(0);
    width: 38px;
    height: 38px;
    // padding: rem(18) rem(18);

    // @include mq("md") {
    //     margin-left: rem(36);
    //     margin-left: rem(31.5);
    // }

}

.pagenavi.page-blogDetail__pagenavi .wp-pagenavi .nextpostslink {
    margin-left: rem(41);
}


.pagenavi .wp-pagenavi .nextpostslink::after {
    content: "";
    position: absolute;
    top: 36%;
    left: 30%;
    transform: translate(-50%, -50%);
    width: rem(10); /*  */
    height: rem(10); /*  */
    border-top: 1px solid $green1; /* */
    border-right: 1px solid $green1;/* */
    display: inline-block; /*  */
    transform: rotate(45deg); /*  */

}
/*=====================================================================
# pagenavipreviouspostslink  .nextpostslink js
======================================================================*/
// hover-effect::after
.pagenavi .wp-pagenavi .previouspostslink.hover-effect::after,
.pagenavi .wp-pagenavi .nextpostslink.hover-effect::after {
    border-top: 1px solid white;
    border-right: 1px solid white;
}
