@use "global" as *;

.toggl {

}

.toggl__container {
    position: relative;
    display: block;
    // width: rem(600);
    margin-top: rem(15);
  }
  
  details {
    position: relative;
    width: auto;
    height: auto;
    overflow: hidden;
    max-height: 2.5em !important;
    transition: all 0.1s ease;
  }
  details ::-webkit-details-marker {
    display: none;
  }
  details > summary {
    position: relative;
    margin-top: rem(1);
    color: $green1;
    padding: rem(2) rem(0) rem(4) rem(0);
    // background: #444;

    font-size: rem(30);
    font-style: normal;
    font-weight: $regular;
    line-height: normal;//
  }
  details > summary:before {
    content: "";
    color: #eee;
    font-size: 0.5em;
    // margin-right: 0.5em;
  }
  details[open] > summary {
    // background: #66e;
    // color: $green1;
    // font-size: rem(24);
    // font-style: normal;
    // font-weight: $regular;
    // line-height: normal;//
  }
  details[open] > summary:before {
    content: "";
    color: #ccc;
  }
  
  .folder {
    margin: rem(-14) rem(0) rem(0) rem(29);
    // padding: 0.5em 0em 0.5em 1.5em;
    // border-left: 0.1em #ccc dotted;
    // border-bottom: 0.1em #ccc dotted;
  }
  .folder p {
    margin: rem(15);
    color: $main-black;
    font-style: normal;
    font-weight: $regular;
    line-height: normal;//
    transition: .5s ;
    cursor: pointer;
  }
  
  details[open] {
    height: auto;
    max-height: 99em !important;
    transition: all 1.2s ease;
  }
  
  details:not([open]) .folder {
    height: rem(0) !important;
  }

  .toggl__age {
    font-size: rem(24);
    transition: .5s ;
    cursor: pointer;
    margin-left: rem(11);

  }

  .toggl__month {
    font-size: rem(24);
    margin-left: rem(-6);
    transition: .5s ;
    cursor: pointer;
    margin-left: rem(6);
  }

  .toggl__age:hover,
  .toggl__month:hover,
  .folder p:hover  {
    opacity: .5;
  }



/* 
=====================================================*/

details > summary {
    list-style: none; /*  */
    cursor: pointer; /*  */
    position: relative; /* ::before */
    padding-left: rem(20); /*  */
}

details > summary::before {
    content: ""; /*  */
    position: absolute;
    left: 1%; /* summary */
    top: 36%; /*  */
    width: rem(17); /*  */
    height: rem(21);
    background-image: url(../images/common/icon-blog-allow.png);
    background-size: contain; /*  */
    transition: transform 0.2s ease-in-out; /*  */
    background-repeat: no-repeat;
}

details[open] > summary::before {
    transform: rotate(90deg); /* 90 */
}
