.btn {
  background: #408F95;
  border: 1px solid #408F95;
  position: relative;
  z-index: 1;
  display: inline-block;
  max-width: 18.75rem;
  padding: 0.875rem 5.4375rem 0.875rem 2.3125rem;
  text-align: center;
  color: #fff;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.57143; /* 157.143% */
  cursor: pointer;
  margin-bottom: 4px;
}

.btn.btn--white {
  background: #fff;
  border: 1px solid #fff;
  color: #408F95;
  padding: 0.875rem 6rem 0.875rem 2.3125rem;
}

.btn.btn--contact {
  padding: 0.875rem 6.625rem 0.875rem 2.9375rem;
}

.btn::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.btn.btn--white::after {
  background: #408F95;
}

@media screen and (min-width: 768px) {
  .btn:hover {
    color: #408F95;
  }
}

@media screen and (min-width: 768px) {
  .btn.btn--white:hover {
    color: #fff;
  }
}

@media screen and (min-width: 768px) {
  .btn:hover::after {
    transform: scale(1, 1);
  }
}

.btn::before {
  position: absolute;
  content: "";
  display: block;
  top: calc(50% - 0.125rem);
  right: 2.5rem;
  width: 2.5rem;
  height: 0.3125rem;
  border: none;
  border-right: 0.125rem solid #FFF;
  border-bottom: 0.0625rem solid #FFF;
  transform: skew(53deg);
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .btn::before {
    right: 2.625rem;
    width: 2.5625rem;
    top: calc(50% - 0.25rem);
  }
}

.btn.btn--white::before {
  border-right: 0.125rem solid #408F95;
  border-bottom: 0.0625rem solid #408F95;
}

.btn.btn--contact::before {
  right: 3.625rem;
}

@media screen and (min-width: 768px) {
  .btn:hover::before {
    border-right: 0.125rem solid #408F95;
    border-bottom: 0.0625rem solid #408F95;
  }
}

@media screen and (min-width: 768px) {
  .btn.btn--white:hover::before {
    border-right: 0.125rem solid #fff;
    border-bottom: 0.0625rem solid #fff;
  }
}

.btn span {
  position: relative; /*  position: relative;  */
  transition: 0.3s;
}

.btn span::after {
  content: "";
  border: 0.0625rem solid #408F95;
  position: absolute;
  top: -1.0625rem;
  right: -7.375rem;
